import { Component, OnInit } from '@angular/core';
import { Vendor_kategori_master } from 'src/app/sdk/asset/models/vendor-_kategori-_master';
import { VendorVendorKategoriMasterService } from 'src/app/sdk/asset/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private vendorVendorKategoriMasterService: VendorVendorKategoriMasterService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    ngOnInit() {
        this.getData()
    }

    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''
    listOfDisplayData: Vendor_kategori_master[] = []
    listOfData: Vendor_kategori_master[] = []
    getData() {
        this.spinner.show()
        this.vendorVendorKategoriMasterService.getVendorVendorKategoriMasters(JSON.stringify({ sortKey: this.sortKey, sortValue: this.sortValue,search: this.search })).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()

            }
        )
    }

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }



    updateFilter(value: string): void {
        this.search = value
        this.getData()
    }



    delete(id) {
        this.spinner.show();
        this.vendorVendorKategoriMasterService.deleteVendorVendorKategoriMastersId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }


    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
