import { Component, OnInit } from '@angular/core';
import { VendorVendorKategoriMasterService } from 'src/app/sdk/asset/services';
import { Vendor_kategori_master } from 'src/app/sdk/asset/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private vendorVendorKategoriMasterService: VendorVendorKategoriMasterService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) { }

    data: Vendor_kategori_master

    ngOnInit() {
        this.form()
    }

    dataForm: FormGroupTypeSafe<Vendor_kategori_master>
    form() {
        this.dataForm = this.fb.group<Vendor_kategori_master>(
            {
                kategori: new FormControl('', [Validators.required]),
                kategori_code: new FormControl(''),
                kategori_desc: new FormControl(''),
            })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.vendorVendorKategoriMasterService.postVendorVendorKategoriMasters(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/vendor/kategori-master/list'])
    }
}
