import { Component, OnInit } from '@angular/core';
import { VendorVendorKategoriMasterService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Vendor_kategori_master } from 'src/app/sdk/asset/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private vendorVendorKategoriMasterService: VendorVendorKategoriMasterService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Vendor_kategori_master = {}

    id_vendor_kategori_master: string = ''
    ngOnInit() {
        this.id_vendor_kategori_master = this.activatedRoute.snapshot.paramMap.get('id')
        this.form()
        this.getData()
    }

    getData() {
        this.vendorVendorKategoriMasterService.getVendorVendorKategoriMastersId(this.id_vendor_kategori_master).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Vendor_kategori_master>
    form() {
        this.dataForm = this.fb.group<Vendor_kategori_master>(
            {
                kategori: new FormControl('', [Validators.required]),
                kategori_code: new FormControl(''),
                kategori_desc: new FormControl(''),
                id_vendor_kategori_master: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.vendorVendorKategoriMasterService.putVendorVendorKategoriMastersId({id: this.id_vendor_kategori_master, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/vendor/kategori-master/list'])
    }

}
